import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ListaProdottiComponent } from './gestione prodotti/lista-prodotti/lista-prodotti.component';
import { DettaglioProdottoComponent } from './gestione prodotti/dettaglio-prodotto/dettaglio-prodotto.component';


const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'home', component: HomeComponent },
  { path: 'lista_prodotti', component: ListaProdottiComponent },
  { path: 'dettaglio_prodotti', component: DettaglioProdottoComponent }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
